<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-alert
                title=""
                type="info"
                show-icon :closable="false">
            </el-alert>
            <!--  -->
            <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
                <el-step title=""></el-step>
            </el-steps>
            <!-- form -->
            <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" label-position="top">
            <!-- tabs -->
                <el-tabs :tab-position="'left'"  v-model="activeIndex" :before-leave="beforeTabsLeave" @tab-click="tabClick">
                    <el-tab-pane label="" name='0'>
                        <el-form-item label="" prop="goods_name">
                            <el-input v-model="addForm.goods_name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_price">
                            <el-input v-model="addForm.goods_price" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_weight">
                            <el-input v-model="addForm.goods_weight" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_number">
                            <el-input v-model="addForm.goods_number" type="number"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="goods_cat">
                           <el-cascader
                            v-model="addForm.goods_cat"
                            :options="cateList"
                            expandTrigger='hover'
                            :props="cateProps"
                            @change="handleChange"></el-cascader>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="1">
                        <!-- item -->
                        <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
                            <el-checkbox-group v-model="item.attr_vals">
                                <el-checkbox border v-for="(cb, i) in item.attr_vals" :key="i" :label="cb"></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="2">
                        <el-form-item v-for="item in onlyTableData" :key="item.attr_id" :label="item.attr_name">
                            <el-input v-model="item.attr_vals"></el-input>
                        </el-form-item>
                    </el-tab-pane>
                    <el-tab-pane label="" name="3">
                        <el-upload
                        :action="uploadURL"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        list-type="picture"
                        :headers="headersObj"
                        :on-success="uploadSuccess">
                        <el-button size="small" type="primary"></el-button>
                        </el-upload>
                    </el-tab-pane>
                    <el-tab-pane label="" name="4">
                        <!--  -->
                        <quill-editor v-model="addForm.goods_introduce"></quill-editor>
                        <el-button type="primary" class="btnSty" @click="add()"></el-button>
                    </el-tab-pane>
                </el-tabs>
            </el-form>
        </el-card>
        <el-dialog
        title=""
        :visible.sync="previewVisible"
        width="50%">
        <img :src="previewURL" class="previewImg">
        </el-dialog>
    </div>
</template>

<script>

import _ from 'lodash'

export default {
    data() {
        return {
            activeIndex: '0',
            // 
            addForm: {
                goods_name: '',
                goods_price: 0,
                goods_weight: 0,
                goods_number: 0,
                // 
                goods_cat: [],
                pics: [],
                goods_introduce: '',
                attrs: []
            },
            // 
            addRules: {
                goods_name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                goods_price: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                goods_weight: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                goods_number: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                goods_cat: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            // 
            cateList: [],
            cateProps: {
                label: 'cat_name',
                value: 'cat_id',
                children: 'children'
            },
            // 
            manyTableData: [],
            // 
            onlyTableData: [],
            // 
            uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
            // 
            headersObj: {
                Authorization: window.sessionStorage.getItem('token')
            },
            // 
            previewURL: '',
            // 
            previewVisible: false
        }
    },
    created() {
        this.getCateList()
    },
    methods: {
        // 
        async getCateList() {
            const { data: res } = await this.$http.get('categories')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.cateList = res.data
            console.log(this.cateList)
        },
        // ,
        handleChange() {
            if (this.addForm.goods_cat.length !== 3) {
                this.addForm.goods_cat = []
            }
            console.log(this.addForm.goods_cat)
        },
        // tab
        beforeTabsLeave(activeName, oldActiveName) {
            if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
                this.$message.error('!')
                return false
            }
        },
        // tab
        async tabClick() {
            if (this.activeIndex === '1') {
                const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                    params: { sel: 'many' }
                })
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                res.data.forEach(item => {
                    item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
                })
                this.manyTableData = res.data
                console.log(this.manyTableData)
            } else if (this.activeIndex === '2') {
                const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
                    params: { sel: 'only' }
                })
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }
                this.onlyTableData = res.data
                console.log(this.onlyTableData)
            }
        },
        // 
        handlePreview(file) {
            console.log(file)
            this.previewURL = file.response.data.url
            this.previewVisible = true
        },
        // 
        handleRemove(file) {
            // splic
            // console.log(file)
            // console.log(this.addForm)
            const path = file.response.data.tmp_path
            const index = this.addForm.pics.findIndex(x => x.pic === path)
            this.addForm.pics.splice(index, 1)
            console.log(this.addForm)
        },
        // 
        uploadSuccess(response) {
            const pathObj = { pic: response.data.tmp_path }
            this.addForm.pics.push(pathObj)
            console.log(this.addForm.pics)
        },
        // 
        async add() {
            this.$refs.addFormRef.validate(async valid => {
                if (!valid) {
                    return this.$message.error('')
                }
                // goods_cat
                // 
                const form = _.cloneDeep(this.addForm)
                form.goods_cat = form.goods_cat.join(',')
                // 
                this.manyTableData.forEach(item => {
                    const newInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals.join(' ')
                    }
                    this.addForm.attrs.push(newInfo)
                })
                // 
                this.onlyTableData.forEach(item => {
                    const newInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals
                    }
                    this.addForm.attrs.push(newInfo)
                })
                form.attrs = this.addForm.attrs
                console.log(form)
                const { data: res } = await this.$http.post('goods', form)
                if (res.meta.status !== 201) {
                return this.$message.error('!')
                }
                this.$router.push('/goods')
                this.$message.success('')
            })
        }
    },
    computed: {
        cateId() {
            if (this.addForm.goods_cat.length === 3) {
                return this.addForm.goods_cat[2]
            }
            return null
        }
    }
}
</script>

<style lang="less" scoped>
    .el-checkbox {
        margin: 0 10px 0 0
    }
    .previewImg {
        width: 100%;
    }
    .btnSty {
        margin-top: 15px;
    }
</style>
