<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row>
                <el-col :span="8">
                    <el-input placeholder="">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="orderList" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="order_number"></el-table-column>
                <el-table-column label="" prop="order_price"></el-table-column>
                <el-table-column label="" prop="order_status">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.order_pay === '1'" type="success"></el-tag>
                        <el-tag v-else type="danger"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="is_send"></el-table-column>
                <el-table-column label="" prop="create_time">
                    <template slot-scope="scope">
                        {{scope.row.create_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <el-button type="primary" size="mini" icon="el-icon-edit" @click="showBox"></el-button>
                    <el-button type="success" size="mini" icon="el-icon-setting" @click="showProgressBox"></el-button>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="eidtDialogVisible"
        width="50%" >
            <el-form :model="editForm" :rules="editRules" ref="editFormRef" label-width="100px">
            <el-form-item label="/" prop="address1">
                <el-cascader
                v-model="editForm.address1"
                :options="citydata"
                expandTrigger='hover'
                ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="address2">
                <el-input v-model="editForm.address2"></el-input>
            </el-form-item>
            </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="eidtDialogVisible = false"> </el-button>
            <el-button type="primary" @click="eidtDialogVisible = false"> </el-button>
        </span>
        </el-dialog>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="progressDialogVisible"
        width="50%" >
            <el-timeline>
                <el-timeline-item
                v-for="(activity, index) in progressInfo"
                :key="index"
                :timestamp="activity.time">
                {{activity.context}}
                </el-timeline-item>
            </el-timeline>
        <span slot="footer" class="dialog-footer">
            <el-button @click="progressDialogVisible = false"> </el-button>
            <el-button type="primary" @click="progressDialogVisible = false"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
import citydata from './citydata.js'
export default {
    data() {
        return {
            // 
            orderList: [],
            // 
            queryInfo: {
                query: '',
                pagenum: 1,
                pagesize: 10
            },
            total: 0,
            // /
            eidtDialogVisible: false,
            // 
            editForm: {
                address1: [],
                address2: ''
            },
            // 
            editRules: {
                address1: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                address2: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            citydata,
            progressDialogVisible: false,
            progressInfo: [],
            progressRules: {}
        }
    },
    created() {
        this.getOrderList()
    },
    methods: {
        async getOrderList() {
            const { data: res } = await this.$http.get('orders', {
                params: this.queryInfo
            })
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            console.log(res.data)
            this.total = res.data.total
            this.orderList = res.data.goods
        },
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getOrderList()
        },
        handleCurrentChange(newNum) {
            this.queryInfo.pagenum = newNum
            this.getOrderList()
        },
        // 
        showBox() {
            this.eidtDialogVisible = true
        },
        editDialogClosed() {
            this.$refs.editFormRef.resetFields()
        },
        // 
        async showProgressBox() {
            const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
            if (res.meta.status !== 200) {
                return this.$message.error('')
            }
            this.progressInfo = res.data
            this.progressDialogVisible = true
            console.log(res.data)
        }
    }
}
</script>

<style lang="less" scope>
    .el-cascader {
        width: 100%;
    }
</style>
